"use client";

import { useEffect } from "react";
import { MapContainer, TileLayer, Marker, Popup, useMap } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { MOCK_TRAFFIC_DATA } from "@/lib/mockData";

// Fix Leaflet marker icon issue
const iconUrl = "https://unpkg.com/leaflet@1.9.3/dist/images/marker-icon.png";
const iconRetinaUrl = "https://unpkg.com/leaflet@1.9.3/dist/images/marker-icon-2x.png";
const shadowUrl = "https://unpkg.com/leaflet@1.9.3/dist/images/marker-shadow.png";

const DefaultIcon = L.icon({
    iconUrl,
    iconRetinaUrl,
    shadowUrl,
    iconSize: [25, 41],
    iconAnchor: [12, 41],
    popupAnchor: [1, -34],
    tooltipAnchor: [16, -28],
    shadowSize: [41, 41],
});

L.Marker.prototype.options.icon = DefaultIcon;

export default function LeafletMap({ center = [19.0760, 72.8777], zoom = 12, markers }) {
    // Default to mock accidents if no markers provided (compatibility with previous usage)
    const displayMarkers = markers || MOCK_TRAFFIC_DATA.accidents.map(a => ({
        id: a.id,
        lat: a.lat,
        lng: a.lng,
        popupContent: (
            <div className="p-2">
                <h3 className="font-bold text-red-600">{a.type}</h3>
                <p className="text-sm">Severity: {a.severity}</p>
            </div>
        )
    }));

    return (
        <div className="w-full h-full min-h-[500px] relative z-0 rounded-xl overflow-hidden border border-border/50">
            <MapContainer
                center={center}
                zoom={zoom}
                style={{ height: "100%", width: "100%" }}
                className="z-0"
            >
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    className="map-tiles"
                />

                <MapController center={center} zoom={zoom} />

                {displayMarkers.map((marker) => (
                    <Marker
                        key={marker.id}
                        position={[marker.lat, marker.lng]}
                    >
                        {marker.popupContent && <Popup>{marker.popupContent}</Popup>}
                    </Marker>
                ))}
            </MapContainer>

            {/* Dark Mode Overlay for "Cyber" look */}
            <div className="absolute inset-0 pointer-events-none bg-indigo-900/10 mix-blend-multiply z-[400]" />
        </div>
    );
}

// Component to handle map interactions or updates
function MapController({ center, zoom }) {
    const map = useMap();

    useEffect(() => {
        map.invalidateSize();
    }, [map]);

    useEffect(() => {
        map.setView(center, zoom, {
            animate: true,
        });
    }, [map, center, zoom]);

    return null;
}
